@charset "UTF-8";
:root {
  --container-width: 1370px;
  --main-outer-width: 100%;
  --black: #000;
  --black20: rgba(0, 0, 0, 0.2);
  --blackligth: #343434;
  --darkgrey: #505050;
  --middlegrey: #999;
  --ligthgrey: #efefef;
  --bggrey: #f2f2f2;
  --white: #fff;
  --white30: rgba(255, 255, 255, 0.3);
  --brown: #d38b00;
  --brown30: rgba(211, 139, 0, 0.3);
  --red: #ff0004;
  --glow-blue: #0059ff;
  --glow-green1: #00ff4d;
  --glow-green2: #00ff9c;
  --glow-green3: #c3ff00;
  --glow-red: #ff0040;
  --glow-orange: #ff6a00;
  --font-family: "Oswald", sans-serif;
  --second-family: "Fira Sans", sans-serif;
  --third-family: "Proxima Nova", sans-serif;
  --font3: "Inter", sans-serif;
  --font4: "Montserrat", sans-serif;
}

@font-face {
  font-family: Oswald;
  src: url("../fonts/Oswald-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: Oswald;
  src: url("../fonts/Oswald-Light.woff2") format("woff2");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: Oswald;
  src: url("../fonts/Oswald-Bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
