*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  margin: 0;
}

input,
button,
textarea {
  padding: 0;
  background: none;
  border: none;
  outline: none;
}

html,
body {
  height: 100%;
  padding: 0;
  scroll-behavior: smooth;
}

body {
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

p {
  text-wrap: pretty;
}

h1, h2, h3, h4, h5, h6 {
  text-wrap: balance;
}

ul,
ol {
  list-style-type: none;
  padding: 0;
}

a {
  text-decoration: none;
}

.main-wrapper-outer {
  width: 100%;
  min-height: 100%;
  max-width: var(--main-outer-width);
  margin-left: auto;
  margin-right: auto;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: stretch;
  overflow: hidden;
}

.main {
  flex-grow: 1;
  flex-shrink: 1;
}

.container {
  margin: 0 auto;
  width: 100%;
  max-width: var(--container-width);
  padding: 0px 15px;
}

.checkboxes {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.checkbox {
  position: relative;
}
.checkbox__input {
  position: absolute;
  width: 0;
  height: 0;
  opacity: 0;
}
.checkbox__input:checked + .checkbox__label .checkbox__icon svg {
  opacity: 1;
}
.checkbox__input._form-error + .checkbox__label .checkbox__icon {
  border: 1px solid var(--red);
}
.checkbox__label {
  cursor: pointer;
  display: inline-flex;
  position: relative;
  gap: 14px;
}
.checkbox__icon {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--white);
  border: 1px solid var(--ligthgrey);
  border-radius: 7px;
  width: 24px;
  height: 24px;
  flex: 0 0 24px;
}
.checkbox__icon svg {
  opacity: 0;
  transition: opacity 0.3s;
  fill: var(--brown);
}
.checkbox__text {
  font-family: var(--second-family);
  font-weight: 400;
  font-size: 16px;
  line-height: 140%;
  color: var(--middlegrey);
}
.checkbox__text a {
  text-decoration: underline;
  text-decoration-skip-ink: none;
  transition: color 0.3s, text-decoration 0.3s;
}
.checkbox__text a:hover {
  text-decoration: none;
  color: var(--brown);
}

.select {
  min-width: 0;
  position: relative;
  z-index: 30;
}
.select__body {
  position: relative;
}
.select__title {
  border: 1px solid #ddd;
  border-radius: 20px;
  padding: 30px 20px;
  width: 100%;
  background: var(--white);
  text-align: left;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.select__title::after {
  content: "";
  transition: transform 0.3s;
  width: 17px;
  height: 10px;
  display: block;
  background-size: 100% 100%;
  background-image: url("data:image/svg+xml;base64,");
}
._select-open .select__title::after {
  content: "";
  transform: rotate(-180deg);
}
.select__value {
  display: flex;
  min-width: 0;
  overflow: hidden;
  align-items: center;
  flex: 1 1 auto;
}
.select__row {
  display: flex;
  align-items: center;
}
.select__content {
  flex: 1 1 auto;
  text-overflow: ellipsis;
  display: flex;
  align-items: center;
  font-weight: 300;
  font-size: 24px;
  line-height: 130%;
  text-transform: uppercase;
}
.select__content span {
  display: block;
  color: var(--middlegrey);
  margin: 0px 0px 0px 10px;
}
.select__text {
  flex: 1 1 auto;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.select__scroll {
  display: flex;
  flex-direction: column;
  gap: 30px;
}
.select__options {
  position: absolute;
  top: 100%;
  min-width: 100%;
  left: 0;
  border: 1px solid #ddd;
  border-radius: 20px;
  padding: 30px 20px;
  border-top: 0;
  max-height: 457px;
  background: var(--white);
  z-index: 40;
  overflow: auto;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.select__options::-webkit-scrollbar {
  display: none;
}
.select__option {
  display: flex;
  width: 100%;
  text-align: left;
  cursor: pointer;
  color: inherit;
  font-weight: 300;
  font-size: 24px;
  line-height: 130%;
  text-transform: uppercase;
  transition: color 0.3s;
}
.select__option span {
  color: var(--middlegrey);
  display: block;
  margin: 0px 0px 0px 10px;
  transition: color 0.3s;
}
.select__option:hover {
  color: var(--brown);
}
.select__option:hover span {
  color: var(--brown);
}
.select__option._select-selected {
  color: var(--brown);
}
.select__option._select-selected span {
  color: var(--brown);
}

