.dropdown {
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  border: 1px solid #ddd;
  border-radius: 20px;
  padding: 10px;
  background: var(--white);
  max-height: 316px;
  opacity: 0;
  gap: 20px;
  transition: opacity 0.3s, right 0.6s;
  pointer-events: none;
  overflow: auto;
  z-index: 50;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.dropdown::-webkit-scrollbar {
  display: none;
}
.menu-open .dropdown {
  opacity: 1;
  pointer-events: auto;
}
@media (max-width: 992px) {
  .menu-open .dropdown {
    right: 0;
  }
}
@media (max-width: 992px) {
  .dropdown {
    position: fixed;
    right: -100%;
    left: auto;
    top: 0;
    width: 250px;
    height: 100vh;
    max-height: 100%;
    border-radius: 0px;
  }
}
@media (max-width: 480px) {
  .dropdown {
    width: 100%;
  }
}
.dropdown .close {
  display: none;
}
@media (max-width: 992px) {
  .dropdown .close {
    display: flex;
  }
}
.dropdown ul li a {
