}

.close {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 60px;
  height: 60px;
  flex: 0 0 60px;
  border-radius: 50%;
  background-color: var(--black);
  transition: background-color 0.3s;
}
.close svg {
